@charset "UTF-8";
html {
  max-width: calc(100vw - 6px);
}

body {
  font-family: "Manrope", sans-serif;
  color: #1b1b1b;
  background: #efeff1;
  position: relative;
}
body.fix {
  overflow: hidden;
}
body.fix::-webkit-scrollbar {
  width: 0;
}
body::-webkit-scrollbar {
  background: rgba(0, 0, 0, 0);
  width: 6px;
  -webkit-appearance: none;
}
body::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0);
}
body::-webkit-scrollbar-thumb {
  width: 6px;
  background: #9373EA;
  border-radius: 8px 8px 8px 8px;
  box-sizing: border-box;
}

img {
  pointer-events: none;
}

h1 {
  font-weight: 600;
  font-size: 120px;
  line-height: 0.9;
  letter-spacing: -0.06em;
}

h2 {
  font-weight: 600;
  font-size: 90px;
  line-height: 0.9;
  letter-spacing: -0.06em;
}

.container {
  max-width: 1840px;
  margin: 0 auto;
  width: calc(100% - 80px);
}

.btn__blc {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: auto;
  border: none;
  padding: 10px 10px 10px 20px;
  border-radius: 40px;
  background: #1b1b1b;
  gap: 40px;
  transition: all 0.3s ease-in-out;
  font-family: "Manrope", sans-serif;
}
.btn__blc span {
  font-weight: 700;
  font-size: 16px;
  line-height: 1.25;
  text-transform: uppercase;
  color: #fff;
}
.btn__blc i {
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease-in-out;
  width: 40px;
  height: 40px;
  padding: 10px;
  border-radius: 100%;
  background: #2d2d2d;
  transform: rotate(-45deg);
}
.btn__blc i img {
  width: 20px;
}
.btn__blc:hover {
  background: #b197f9;
}
.btn__blc:hover i {
  transform: rotate(0);
}
.btn__violet {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: auto;
  border: none;
  padding: 10px 10px 10px 20px;
  border-radius: 40px;
  background: #9373ea;
  gap: 40px;
  transition: all 0.3s ease-in-out;
  font-family: "Manrope", sans-serif;
}
.btn__violet span {
  font-weight: 700;
  font-size: 16px;
  line-height: 1.25;
  text-transform: uppercase;
  color: #fff;
}
.btn__violet i {
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease-in-out;
  width: 40px;
  height: 40px;
  padding: 10px;
  border-radius: 100%;
  background: #1b1b1b;
  transform: rotate(-45deg);
}
.btn__violet i img {
  width: 20px;
}
.btn__violet:hover {
  background: #b197f9;
}
.btn__violet:hover i {
  background: #2d2d2d;
  transform: rotate(0);
}

.input {
  box-sizing: border-box;
  padding: 20px;
  border-radius: 40px;
  background: #fff;
  border: none;
  font-family: "Manrope", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.4;
  letter-spacing: -0.03em;
  color: rgba(27, 27, 27, 0.7019607843);
  width: 100%;
  border: 1px solid #fff;
  transition: all 0.3s;
  resize: none;
  color: #1b1b1b;
}
.input::placeholder {
  color: rgba(27, 27, 27, 0.7019607843);
  transition: all 0.3s;
}
.input:hover {
  color: #1b1b1b;
}
.input:hover::placeholder {
  color: #1b1b1b;
}
.input:focus {
  color: #1b1b1b;
}
.input.error {
  border: 1px solid #eb5757;
}
.input.error::placeholder {
  color: #de5d5d;
}
.input.error + p {
  opacity: 1;
}
.input__wrapper {
  position: relative;
}
.input__wrapper p {
  font-family: "Manrope", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;
  text-align: right;
  color: #de5d5d;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 20px;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
}
.input__wrapper p.text__email {
  opacity: 0;
}
.input__wrapper p.error {
  color: #de5d5d;
}
.input__wrapper p.error span {
  color: #de5d5d;
}
.input__wrapper p.error span.error {
  color: #de5d5d;
}
.input__wrapper.error__email p {
  opacity: 0;
}
.input__wrapper.error__email p.text__email {
  opacity: 1;
}
.input__file {
  position: absolute;
  inset: 0;
  opacity: 0;
  cursor: pointer;
  z-index: 2;
}
.input__file_wrapper {
  position: relative;
}
.input__file_btn {
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #fff;
  border-radius: 40px;
  cursor: pointer;
}
.input__file_btn p {
  font-family: "Manrope", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.4;
  letter-spacing: -0.03em;
  color: #1b1b1b;
  opacity: 0.7;
  text-align: left;
  position: relative;
  inset: auto;
  transform: none;
  transition: all 0.3s;
  cursor: pointer;
}
.input__file:hover + .input__file_btn p {
  opacity: 1;
}
.input__file_list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 20px 0 0;
  gap: 10px;
}
.input__file_list div {
  padding: 10px;
  display: grid;
  position: relative;
  grid-template-columns: 1fr 24px;
  gap: 20px;
  align-items: center;
  border: 1px solid #fff;
  border-radius: 40px;
}
.input__file_list p {
  font-family: "Manrope", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;
  color: #fff;
  white-space: nowrap;
  position: relative;
  text-overflow: ellipsis;
  overflow: hidden;
}
.input__file_list span {
  padding: 10px;
  border-radius: 40px;
  background: #1b1b1b;
  font-family: "Manrope", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;
  color: #fff;
  position: absolute;
  top: 34px;
  right: -20px;
  white-space: nowrap;
  display: block;
  transition: all 0.3s;
  opacity: 0;
  pointer-events: none;
  z-index: 2;
}
.input__file_rem {
  cursor: pointer;
  opacity: 0.5;
  transition: all 0.3s;
  width: 100%;
}
.input__file_rem:hover {
  opacity: 1;
}
.input__file_rem:hover + span {
  opacity: 1;
}

textarea.input {
  height: 164px;
}

.element-animation {
  opacity: 0;
  transition: all 1s;
  transform: translateY(100%);
}

.element-show {
  opacity: 1;
  transform: translateY(0);
}

.nav {
  padding: 20px 0;
  background: #efeff1;
  transform: translateY(0);
  transition: all 0.3s;
  transition-property: transform, opacity;
  position: relative;
}
.nav.fix {
  position: sticky;
  top: -100px;
  left: 0;
  margin: 0;
  z-index: 100;
  transform: translateY(100px);
  width: 100%;
  background: transparent;
}
.nav.fix .nav__logo,
.nav.fix .nav__list,
.nav.fix .btn__blc {
  opacity: 0;
  pointer-events: none;
  transition: all 0s;
}
.nav.fix .nav__menu .btn__blc {
  opacity: 1;
}
.nav .modal__call {
  padding: 10px 10px 10px 20px;
}
.nav__row {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.nav__logo {
  display: block;
  width: 100%;
  max-width: 288px;
}
.nav__logo img {
  width: 100%;
  display: block;
}
.nav__list {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 80px;
}
.nav__list a {
  font-weight: 500;
  font-size: 20px;
  line-height: 1.4;
  letter-spacing: -0.03em;
  color: #1b1b1b;
  transition: all 0.3s ease-in-out;
  position: relative;
}
.nav__list a::before {
  content: "";
  display: block;
  width: 0;
  height: 2px;
  right: 0;
  position: absolute;
  bottom: 0;
  transition: all 0.3s ease-in-out;
  background: #9373ea;
}
.nav__list a:hover {
  color: #9373ea;
}
.nav__list a:hover::before {
  width: 100%;
  right: auto;
  left: 0;
}
.nav__burger {
  margin: 0 0 0 10px;
  width: 60px;
  height: 60px;
  border-radius: 100%;
  position: relative;
  padding: 22px 15px;
  background: #9373ea;
  transition: background 0.3s ease-in-out;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  cursor: pointer;
}
.nav__burger::before, .nav__burger::after,
.nav__burger span {
  content: "";
  display: block;
  width: 30px;
  height: 2px;
  border-radius: 1px;
  background: #fff;
  transition: all 0.3s ease-in-out;
}
.nav__burger span {
  max-width: 21px;
}
.nav__burger::before, .nav__burger::after {
  position: absolute;
  top: 22px;
  left: 50%;
  transform: translateX(-50%);
}
.nav__burger::after {
  bottom: 22px;
  top: auto;
}
.nav__burger:hover {
  background: #b197f9;
}
.nav__burger:active {
  background: #9373ea;
}
.nav__burger.active span {
  opacity: 0;
}
.nav__burger.active::before, .nav__burger.active::after {
  width: 27px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
}
.nav__burger.active::after {
  transform: translate(-50%, 50%) rotate(-45deg);
  top: auto;
  bottom: 50%;
}
.nav__menu {
  border-radius: 40px;
  background: #fff;
  padding: 60px 40px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
  align-items: start;
  height: max-content;
  min-height: 100%;
}
.nav__menu_overflow {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: #efeff1;
  opacity: 0;
  z-index: -1;
  transition: all 0.3s;
  pointer-events: none;
}
.nav__menu ul.nav__menu_case {
  margin: 92px 0 0;
}
.nav__menu ul.nav__menu_case a {
  font-family: "Manrope", sans-serif;
  font-weight: 600;
  font-size: 36px;
  line-height: 1;
  letter-spacing: -0.03em;
  color: #1b1b1b;
  opacity: 1;
}
.nav__menu ul.nav__menu_case a:hover {
  color: #9373EA;
}
.nav__menu ul.nav__menu_case ol a {
  font-family: "Manrope", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.4;
  letter-spacing: -0.03em;
  color: #1b1b1b;
  opacity: 0.7;
}
.nav__menu ul.nav__menu_case ol a:hover {
  opacity: 1;
}
.nav__menu_wrapper {
  z-index: 10;
  width: calc(100% + 6px);
  height: calc(100vh - 100px);
  opacity: 0;
  pointer-events: none;
  position: absolute;
  transform: translateY(-100%);
  transition: all 0.3s;
  top: calc(100% + 19px);
  left: 0;
  transform: translateY(-100%);
  position: absolute;
  overflow-y: scroll;
}
.nav__menu_wrapper::-webkit-scrollbar {
  background: rgba(0, 0, 0, 0);
  width: 6px;
  -webkit-appearance: none;
}
.nav__menu_wrapper::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0);
}
.nav__menu_wrapper::-webkit-scrollbar-thumb {
  width: 6px;
  background: #9373EA;
  border-radius: 8px 8px 8px 8px;
  box-sizing: border-box;
}
.nav__menu_btn {
  display: none;
}
.nav__menu h3,
.nav__menu .h3 {
  font-weight: 600;
  font-size: 60px;
  line-height: 0.9;
  letter-spacing: -0.06em;
  color: #1b1b1b;
}
.nav__menu h3 span,
.nav__menu .h3 span {
  color: #9373ea;
}
.nav__menu h4 {
  font-weight: 600;
  font-size: 36px;
  line-height: 1;
  letter-spacing: -0.03em;
  color: #1b1b1b;
}
.nav__menu_block {
  height: 100%;
  grid-column: 2/3;
  grid-row: 1/3;
  display: grid;
  grid-template-rows: 1fr auto;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
}
.nav__menu_block .nav__info {
  display: none;
}
.nav__menu_block .btn__blc {
  gap: 0px;
  grid-column: 2/3;
  order: 5;
  padding: 20px 20px 20px 30px;
}
.nav__menu_list {
  position: relative;
  display: flex;
  flex-direction: column;
}
.nav__menu_list ul {
  margin: 40px 0 0;
  display: grid;
  grid-template-columns: 100%;
  gap: 20px;
}
.nav__menu_list a {
  font-weight: 500;
  font-size: 20px;
  line-height: 1.4;
  letter-spacing: -0.03em;
  color: #1b1b1b;
  transition: all 0.3s;
  opacity: 0.7;
}
.nav__menu_list a:hover {
  color: #9373ea;
  opacity: 1;
}
.nav__menu_list svg {
  display: none;
}
.nav__menu_list .menu__call ul {
  position: absolute;
  top: 0;
  background: #efeff1;
  padding: 40px;
  display: grid;
  grid-template-columns: 100%;
  gap: 10px;
  border-radius: 20px;
  width: 100%;
  z-index: 2;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
}
.nav__menu_list .menu__call:hover ul {
  opacity: 1;
  pointer-events: auto;
}
.nav__menu_list:nth-child(1) .menu__call ul {
  left: calc(100% + 40px);
}
.nav__menu_list:nth-child(1) .menu__call ul::before {
  content: "";
  display: block;
  width: 50px;
  height: 100%;
  right: 100%;
  position: absolute;
}
.nav__menu_list:nth-child(2) .menu__call ul {
  right: calc(100% + 40px);
}
.nav__menu_list:nth-child(2) .menu__call ul::before {
  content: "";
  display: block;
  width: 50px;
  height: 100%;
  left: 100%;
  position: absolute;
}
.nav__info {
  margin: auto 0 0;
}
.nav__info .nav__soc {
  display: none;
}
.nav__contact {
  display: grid;
  grid-template-columns: 100%;
  gap: 20px;
}
.nav__contact a,
.nav__contact p {
  transition: all 0.3s;
  font-weight: 600;
  font-size: 36px;
  line-height: 1;
  letter-spacing: -0.03em;
  color: #1b1b1b;
  opacity: 0.7;
}
.nav__contact a:hover {
  opacity: 1;
}
.nav__soc {
  display: grid;
  grid-template-columns: 60px 60px 60px;
  gap: 10px;
  align-items: end;
}
.nav__soc a {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #1b1b1b;
  border-radius: 100%;
  width: 60px;
  height: 60px;
}
.nav__soc a:hover img {
  opacity: 1;
}
.nav__soc img {
  width: 36px;
  display: block;
  transition: all 0.3s;
  opacity: 0.7;
}
.nav .container {
  position: relative;
}
.nav.active {
  z-index: 100;
}
.nav.active .btn__blc {
  opacity: 0;
}
.nav.active .nav__menu .btn__blc {
  opacity: 1;
  pointer-events: auto;
  transition: all 0.3s;
}
.nav.active .nav__menu_wrapper {
  opacity: 1;
  pointer-events: auto;
  transform: translateY(0);
}
.nav.active .nav__menu_overflow {
  opacity: 1;
}
.nav.active .nav__list {
  opacity: 0;
}
.nav.active.fix .nav__logo {
  opacity: 1;
}

.header {
  position: relative;
  overflow: hidden;
}
.header__mob {
  display: none;
  cursor: pointer;
}
.header__block {
  background: url("../img/header_bg.jpg") center no-repeat;
  background-color: #1b1b1b;
  background-size: cover;
  border-radius: 40px;
  overflow: hidden;
  position: relative;
  z-index: 20;
}
.header__info {
  padding: 60px 40px;
  max-width: 60%;
  backdrop-filter: blur(10px);
  background: rgba(255, 255, 255, 0.01);
  position: relative;
  overflow: hidden;
  mask-image: linear-gradient(to right, black 80%, transparent 95%);
  border-top-left-radius: 100px;
  border-bottom-left-radius: 100px;
}
.header__logo {
  position: absolute;
  top: 15px;
  right: 120px;
  z-index: -1;
}
.header__logo_video {
  top: -200px;
  right: -400px;
}
.header__logo video {
  width: 2200px;
  max-width: none;
  object-fit: contain;
}
.header h1 {
  font-weight: 600;
  font-size: 120px;
  line-height: 0.9;
  letter-spacing: -0.06em;
  color: #fff;
}
.header h1 span {
  display: block;
  color: #9373ea;
}
.header__text {
  margin: 365px 0 0;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.4;
  letter-spacing: -0.03em;
  color: #fff;
  max-width: 380px;
}
.header__video {
  position: absolute;
  bottom: 60px;
  right: 40px;
}
.header__video_block {
  width: 195px;
  height: 255px;
  border-radius: 20px;
  overflow: hidden;
}
.header__video_look {
  margin: 0 0 20px;
  max-width: 209px;
}
.header__video_look img {
  width: 100%;
}
.header__video_wrapper {
  width: 195px;
  height: 255px;
  border-radius: 100%;
  border-radius: 20px;
  position: relative;
  overflow: hidden;
  object-fit: cover;
  transition: all 0.3s;
  background: #000;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__video_wrapper iframe {
  height: 100%;
  width: 50vh;
  margin: auto;
}
.header__video_wrapper iframe .videoplayer--is-short-video-embed:not(.videoplayer--fullscreen) .videoplayer_media_provider {
  object-fit: contain !important;
}
.header__video_wrapper video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 1;
}
.header__video_wrapper.active {
  top: 0 !important;
  left: 0 !important;
  width: 100%;
  height: 100%;
  border-radius: 0;
  z-index: 20;
}
.header__video_wrapper.active video {
  object-fit: contain;
}
.header__video_wrapper.active .header__video_play {
  opacity: 0;
}
.header__video_wrapper.active .header__video_close {
  opacity: 1;
  pointer-events: auto;
}
.header__video_play {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60px;
  height: 60px;
  border-radius: 100%;
  background: #1b1b1b;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s;
  z-index: 1;
}
.header__video_play img {
  width: 21px;
}
.header__video_play:hover {
  background: #b197f9;
}
.header__video_close {
  position: absolute;
  top: 40px;
  right: 40px;
  width: 60px;
  height: 60px;
  border-radius: 100%;
  background: #9373ea;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s;
  opacity: 0;
  pointer-events: none;
  cursor: pointer;
  z-index: 2;
}
.header__video_close img {
  width: 36px;
}
.header__video_close:hover {
  background: #b197f9;
}

.service {
  padding: 40px 0;
  position: relative;
}
.service .container {
  position: sticky;
  top: 40px;
}
.service__block {
  border-radius: 40px;
  background: #fff;
  padding: 60px 40px;
}
.service__tag {
  background: #efeff1;
  border-radius: 40px;
  padding: 10px 30px;
  font-family: "Manrope", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.4;
  letter-spacing: -0.03em;
  color: #1b1b1b;
  display: inline-block;
}
.service__row {
  margin: 20px 0 0;
  display: grid;
  grid-template-columns: 1fr 248px;
  gap: 40px;
  align-items: end;
}
.service__row h2 {
  font-family: "Manrope", sans-serif;
  font-weight: 600;
  font-size: 90px;
  line-height: 0.9;
  letter-spacing: -0.06em;
  color: #1b1b1b;
}
.service__row h2 span {
  color: #9373ea;
}
.service__row .btn__blc {
  gap: 0;
  padding: 20px 20px 20px 30px;
}
.service__wrapper {
  margin: 40px 0 0;
  position: relative;
  overflow: hidden;
}
.service__scroll {
  display: flex;
  gap: 40px;
  width: max-content;
}
.service__scroll .btn__blc,
.service__scroll .btn__violet {
  display: none;
}
.service__item {
  width: 1095px;
  height: 479px;
  background: #efeff1;
